@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
  border: 0;
  margin: 0;
  padding: 0;
}

footer, header, nav, section, main {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  -webkit-appearance: none;
  border-radius: 0;
}

a {
  text-decoration: none;
}

:root {
  --text-color: #252b42;
  --second-text-color: #737373;
  --second-bg-color: #17213c;
  --prymary-color: #ff7b47;
  --muted-color: #bdbdbd;
  --light-text-color: #fff;
}

.container-nav {
  height: 65px;
  background-color: var(--light-text-color);
  justify-content: space-between;
  align-items: center;
  padding: 0 35px;
  display: flex;
  position: relative;
}

.navbar {
  width: 100%;
  box-shadow: 0 1px 4px rgba(146, 161, 176, .15);
}

.navbar__logo {
  letter-spacing: .1px;
  color: var(--dark-text-color);
  font-family: Montserrat, sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.3;
}

.navbar__checkbox {
  width: 32px;
  height: 32px;
  z-index: 9;
  opacity: 0;
  cursor: pointer;
  display: block;
  position: absolute;
  right: 20px;
}

.navbar__burger-lines {
  height: 22px;
  width: 31px;
  z-index: 6;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  position: absolute;
  right: 20px;
  transform: rotate(180deg);
}

.navbar__line {
  height: 3px;
  width: 100%;
  background-color: var(--second-text-color);
  border-radius: 10px;
}

.navbar__checkbox:checked ~ .navbar__burger-lines .line1 {
  width: 90%;
  transform: rotate(45deg);
}

.navbar__checkbox:checked ~ .navbar__burger-lines .line2 {
  transform: scaleY(0);
}

.navbar__checkbox:checked ~ .navbar__burger-lines .line3 {
  width: 90%;
  transform: rotate(-45deg);
}

.navbar__items {
  height: 100vh;
  text-align: center;
  z-index: 5;
  background-color: #f6f6f6;
  padding-top: 55px;
  transition: transform .5s ease-in-out;
  position: absolute;
  top: 65px;
  bottom: 0;
  left: 0;
  right: 0;
  transform: translateX(-150%);
}

.navbar__item:first-child {
  margin-top: 80px;
}

.navbar__item:not(:last-child) {
  margin-bottom: 30px;
}

.navbar__link {
  color: var(--second-text-color);
  letter-spacing: .2px;
  font-size: 30px;
  font-weight: 400;
  line-height: 1.5;
}

.line1 {
  width: 50%;
  transform-origin: 0 0;
  transition: transform .4s ease-in-out;
}

.line2 {
  width: 75%;
  transition: transform .2s ease-in-out;
}

.line3 {
  width: 100%;
  transform-origin: 0 100%;
  transition: transform .4s ease-in-out;
}

.container-nav .navbar__checkbox:checked ~ .navbar__items {
  transform: translateY(0);
}

.container-header {
  padding: 0 42px;
}

.header {
  background-color: #fafafa;
  padding-top: 80px;
  padding-bottom: 50px;
}

.header__title {
  text-align: center;
  letter-spacing: .2px;
  color: var(--text-color);
  margin-bottom: 40px;
  font-family: Montserrat, sans-serif;
  font-size: 40px;
  font-weight: 700;
  line-height: 1.42;
}

.header__text {
  text-align: center;
  letter-spacing: .2px;
  color: var(--second-text-color);
  margin-bottom: 40px;
  font-family: Montserrat, sans-serif;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.5;
}

.header__links {
  text-align: center;
  letter-spacing: .2px;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 2;
}

.header__link {
  color: var(--light-text-color);
  background-color: #ff7b47;
  border-radius: 37px;
  padding: 10px 36px;
  display: inline-block;
}

.header__link:not(:first-child) {
  background-color: var(--light-text-color);
  color: #0d5c63;
  border: 1px solid #0d5c63;
  margin-top: 25px;
}

